let baseUrl = 'https://bjshqcyb.com/api'

// token
let getStorage = (key) => {
	return uni.getStorageSync(key)
}
// token
/**
 * 
 * @param {token} key token 
 * @param {relosuser} key  (10 )
 * @param {SHrelos} key  (1 )
 * @param {JBname} key 
 * @param {*} key
 */
let setStorage = (key, val) => {
	uni.setStorageSync(key, val)
}
// get
let get = (url, data) => {	
	let promise = new Promise((resolve, reject, ContentType) => {
		uni.request({
			url: uni.getStorageSync('SYSTEMURL') + url,
			method: 'GET',
			data: data,
			header: {
				'Content-Type': ContentType,
				'token': uni.getStorageSync('token')
			},
			success: (res) => {
				console.log(uni.getStorageSync('SYSTEMURL'));
				if(res.statusCode == 200) {
					if(res.data.code == 413){
						setStorage('token', '')
						uni.redirectTo({
							url: '/pages/login/login'
						})
					}else {
						resolve(res)
					}
				}else {
					reject(res)
				}
			},
			fail: (err) => {
				reject(err)
			}
		})
	})
	return promise
}
// post
let post = (url, data, ContentType) => {	
	let promise = new Promise((resolve, reject) => {
		uni.request({
			url: uni.getStorageSync('SYSTEMURL') + url,
			method: 'POST',
			data: data,
			header: {
				// 'Content-Type': 'application/x-www-form-urlencoded',
				// 'Content-Type': 'application/json;charset=UTF-8',
				'Content-Type': ContentType,
				'token': uni.getStorageSync('token')
			},
			success: (res) => {
				console.log(uni.getStorageSync('SYSTEMURL'));
				if(res.statusCode == 200) {
					if(res.data.code == 413){
						setStorage('token', '')
						uni.redirectTo({
							url: '/pages/login/login'
						})
					}else {
						resolve(res)
					}
				}else {
					reject(res)
				}
			},
			fail: (err) => {
				reject(err)
			}
		})
	})
	return promise
}
// 
let routerIntercept = (that) => {
	let routerType = ['navigateTo', 'redirectTo', 'reLaunch', 'navigateBack', 'switchTab']
	routerType.forEach(item => {
		uni.addInterceptor(item, {
			invoke(args) {
				let token = getStorage('token')
				if(token) {
					if(args.url == '/pages/login/login') {
						return false
					}else {
						return true
					}
				}else {
					if(args.url != '/pages/login/login') {
						return false
					}else {
						return true
					}
				}
			}
		})
	})
	
}
// onshow
let goLogin = () => {
	// home
	uni.hideHomeButton()
	// tokenlogin
	if(!getStorage('token')) {
		uni.redirectTo({
			url: '/pages/login/login'
		})
	}
}
// token
let clearToken = () => {
	// 
	setStorage('JBname', '')
	// token
	setStorage('token', '')
	// 
	setStorage('relosuser', '')
	// 
	setStorage('SHrelos', '')
	uni.reLaunch({
		url: '/pages/login/login'
	})
}
let API = {
	getStorage,
	setStorage,
	get,
	post,
	routerIntercept,
	goLogin,
	clearToken,
	baseUrl,
}
export default API